html {
  font-size: 62.5%;
}

body {
  margin: 10;
  font:-300 11px/1.4 'Helvetica Neue', 'sans-serif';
  color: #669bbc;
  background: #003049;
  overflow: hidden;
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
