.home-page {

    .text-zone {
        position: absolute;
        left: 12%;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 90%;
    }
    .resume {
        color: #ffd700;
        background-color: #003049;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        margin-top: 25px;
        float: left;
        animation: fadeIn ls 1.8s backwards;
        white-space: nowrap;
    }
    h1 {
        color: #fff;
        font-size: 53px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;
        animation: fadeIn 1ms backwards;

        &::before {
            content: 'Welcome to the Portfolio of: ';
            font-family: 'La Belle Aurore';
            color: #ffd700;
            font-size: 18px;
            position: absolute;
            margin-top: -40px;
            left: 0px;
            opacity: 0.6;
        }

        &::after {
            font-family: 'La Belle Aurore';
            color: #ffd700;
            font-size: 18px;
            position: absolute;
            margin-top: 18px;
            margin-left: 28px;
            animation: fadeIn ls .1s backwards;
            opacity: 0.6;
        }

        img {
            width: 32px;
            margin-left: 20px;
            opacity: 0;
            height: auto;
            animation: rotateIn ls linear both;
            animation-delay: 1.4s;
        }

        h2 {
            color: #8d8d8d;
            margin-top: 20px;
            font-weight: 400;
            font-size: 13px;
            font-family: sans-serif;
            letter-spacing: 3px; 
            animation: fadeIn ls 1.8 backwards;
        }

        .flat-button {
            color: #ffd700;
            font-size: 13px;
            font-weight: 400;
            letter-spacing: 4px;
            font-family: sans-serif;
            text-decoration: none;
            padding: 10px 18px;
            border: 1px solid #ffd700;
            margin-top: 25px;
            float: left;
            animation: fadeIn ls 1.8s backwards;
            white-space: nowrap;
        }
        
        &:hover {
            background: #ffd700;
            color: #ffd;
        }
    }
    :any-link {
        color: #ffd700;
        font-size: 10px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #ffd700;
        margin-top: 25px;
        float: left;
        white-space: nowrap;
    }
}
